import { USERSTATE_LOGIN } from '../actions/userstate'

export const INITIAL_STATE = {

	isLogin: false,
	uid: ''

};


export default ( state = INITIAL_STATE, action) => {


	switch(action.type){

		case USERSTATE_LOGIN:
			return Object.assign({}, state, {
				isLogin: true,
				uid: action.uid
			});
		default:
			return state;

	}


}


